import { login } from "Apis/Auth";
import React, { useState } from "react";
import { Container, Row, Col, Form, Button } from "react-bootstrap";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";
import { toast } from "react-toastify";

function Login() {
  const history = useHistory();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  const gotodashboard = async (event) => {
    event.preventDefault();
    if (username.trim() === "" || password.trim() === "") {
      setError("Username and password are required.");
      toast.error("Username and password are required.");
      return;
    }
    const data = await login({ username, password });
    if (data?.errors === null) {
      const expirationTime = new Date().getTime() + 8 * 60 * 60 * 1000; // 8 hours in milliseconds
      const userData = {
        ...data,
        expirationTime,
      };
      localStorage.setItem("userData", JSON.stringify(userData));
      toast.success("Login Successfull.");
      history.push("/admin/dashboard");
    } else {
      setError("Login failed. Please check your credentials and try again.");
      toast.error("Login failed. Please check your credentials and try again.");
    }
  };
  return (
    <div className="App">
      <Container fluid>
        <Row>
          <Col md={6}>
            <div className="login-intro-text"></div>

            <Form className="loginform" onSubmit={gotodashboard}>
              <img
                className="img-logo"
                src="data:image/png;base64,"
              />
              <h2 className="text-center mb-4">Welcome back</h2>
              <p className="text-center login-text-2">
                Please enter your details to sign you in
              </p>
              <Form.Group controlId="formBasicEmail">
                <Form.Label>Username or Mobile#</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter your username"
                  onChange={(e) => setUsername(e.target.value)}
                />
              </Form.Group>

              <Form.Group controlId="formBasicPassword" style={{marginTop:'10px'}}>
                <Form.Label>Password</Form.Label>
                <Form.Control
                  type="password"
                  placeholder="Password"
                  onChange={(e) => setPassword(e.target.value)}
                />
              </Form.Group>

              <Row className="align-items-center">
                <Col xs={6}></Col>
                <Col xs={6} className="forgot-password">
                  <a href="/forgetpassword" style={{ color: "#cba640" }}>
                    Forgot password?
                  </a>
                </Col>
              </Row>
              <div>
                <Button
                  variant="primary"
                  className="login-btn-submit "
                  type="submit"
                  block
                >
                  Sign in
                </Button>
              </div>
            </Form>
          </Col>
          <Col md={6} className="image-section"></Col>
        </Row>
      </Container>
    </div>
  );
}

export default Login;
